<script>
import { reactive, toRefs, ref } from "vue";
import { useRouter } from "vue-router";

export default {
    name: "Register",
    components: {},
    setup() {
        const router = useRouter();
        const state = reactive({
            ruleForm: {},
        });

        const register = (ruleForm) => {
            loginApi.register(ruleForm).then((res) => {
                if (res.data.code === 0) {
                    ElMessage.success("注册成功！");
                    setTimeout(() => {
                        router.push("/home");
                    }, 1000);
                }
            });
        };

        return {
            ...toRefs(state),
            register,
            router,
        };
    },
};
</script>
